/*  header-major ================================================================= */
#header-major {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100vw;
  min-width: 320px;
  max-height: 1400px;
  height: 100vh;
  background-color: #161418;
  /* variables п.5 */
  background-image: url('../../build/img/header_background-image2000x1333.png');
  /* variables п.4 */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  margin: 0;
  padding: 0;
}
#header-major .wrapper-menu-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 80px;
  border: 1px solid #ffffff;
  /* variables п.6 */
  cursor: pointer;
  position: fixed;
  top: 3%;
  right: 3%;
  transition: background-color 0.3s linear, border-radius 0.3s linear;
  z-index: 1000;
  /* mixin п.1 */
}
#header-major .wrapper-menu-icon:hover {
  background-color: #ffffff;
  /* variables п.6 */
}
#header-major .wrapper-menu-icon:hover .wrapper-menu-icon__burger span,
#header-major .wrapper-menu-icon:hover .wrapper-menu-icon__burger::after,
#header-major .wrapper-menu-icon:hover .wrapper-menu-icon__burger::before {
  background-color: #161418;
  /* variables п.6 */
}
#header-major .wrapper-menu-icon.active {
  background-color: #ffffff;
  /* variables п.6 */
}
#header-major .wrapper-menu-icon__burger {
  display: block;
  position: relative;
  height: 30px;
  width: 40px;
}
#header-major .wrapper-menu-icon__burger__span {
  position: absolute;
  background-color: #ffffff;
  /* variables п.6 */
  width: 100%;
  height: 3px;
  left: 0;
  top: 13px;
  transition: all 0.3s ease 0s;
}
#header-major .wrapper-menu-icon__burger__span:hover {
  background-color: #161418;
  /* variables п.6 */
}
#header-major .wrapper-menu-icon__burger__span.active {
  transform: scale(0);
  background-color: #161418;
  /* variables п.6 */
}
#header-major .wrapper-menu-icon__burger::before,
#header-major .wrapper-menu-icon__burger::after {
  content: "";
  background-color: #ffffff;
  /* variables п.6 */
  position: absolute;
  width: 100%;
  height: 3px;
  left: 0;
  transition: all 0.3s ease 0s;
}
#header-major .wrapper-menu-icon__burger::before {
  top: 0;
}
#header-major .wrapper-menu-icon__burger::after {
  bottom: 0;
}
#header-major .wrapper-menu-icon__burger.active::before {
  transform: rotate(45deg);
  top: 13px;
  background-color: #161418;
  /* variables п.6 */
}
#header-major .wrapper-menu-icon__burger.active::after {
  transform: rotate(-45deg);
  bottom: 13px;
  background-color: #161418;
  /* variables п.6 */
}
@media (max-width: 1199px) {
  #header-major .wrapper-menu-icon {
    height: 70px;
    width: 70px;
  }
}
@media (max-width: 991px) {
  #header-major .wrapper-menu-icon {
    height: 60px;
    width: 60px;
  }
  #header-major .wrapper-menu-icon__burger {
    height: 23px;
    width: 33px;
  }
  #header-major .wrapper-menu-icon__burger__span {
    top: 10px;
  }
  #header-major .wrapper-menu-icon__burger.active::before {
    top: 10px;
  }
  #header-major .wrapper-menu-icon__burger.active::after {
    bottom: 10px;
  }
}
@media (max-width: 767px) {
  #header-major .wrapper-menu-icon {
    height: 50px;
    width: 50px;
  }
  #header-major .wrapper-menu-icon__burger {
    height: 13px;
    width: 16px;
  }
  #header-major .wrapper-menu-icon__burger__span {
    top: 5px;
  }
  #header-major .wrapper-menu-icon__burger.active::before {
    top: 5px;
  }
  #header-major .wrapper-menu-icon__burger.active::after {
    bottom: 5px;
  }
