/* main-education ========================================================================================== */
/* 1. media запросы для шрифтов находятся в файле font.less 
   2. media запросы для эле  ментов (.wrapper-menu-icon; .wrapper-menu-item-navigation; .header-major__wrapper-arrow) 
    находятся в файле vatiables.less */
@media (max-width: 1199px) {
  #header-major {
    background-image: url('../../build/img/header_background-image1500x999.png');
    /* variables п.4 */
  }
  #header-major .header-major__content__figure {
    height: 480px;
  }
  #header-major .header-major__content__figure__img {
    width: 310px;
    height: 303px;
  }
  #header-major .header-major__content__figure__figcaption {
    margin: -1px 0 0 -2px;
  }
  #header-major .header-major__content__wrapper-text p {
    margin: 25px 0 0 0;
  }
  #header-major .header-major__content__wrapper-text__contacts {
    margin: 25px 0 0 0;
  }
  #header-major .header-major__content__wrapper-text__contacts__icon {
    width: 30px;
    height: 33px;
  }
  #header-major .header-major__content__wrapper-text__contacts p {
    margin: 0;
  }
}
@media (max-width: 991px) {
  #header-major {
    background-image: url('../../build/img/header_background-image1200x666.png');
    /* variables п.4 */
  }
  #header-major .header-major__content__figure {
    height: 410px;
  }
  #header-major .header-major__content__figure__img {
    width: 250px;
    height: 250px;
  }
  #header-major .header-major__content__figure__figcaption {
    margin: -1px 0 0 -1px;
    line-height: 43px;
  }
  #header-major .header-major__content__wrapper-text p {
    margin: 15px 0 0 0;
  }
  #header-major .header-major__content__wrapper-text__contacts {
    margin: 15px 0 0 0;
  }
  #header-major .header-major__content__wrapper-text__contacts__icon {
    width: 20px;
    height: 23px;
  }
  #header-major .header-major__content__wrapper-text__contacts p {
    margin: 0;
  }
}
@media (max-width: 767px) {
  #header-major {
    background-image: url('../../build/img/header_background-image900x599.png');
  }
  #header-major .header-major__content {
    flex-direction: column;
    justify-content: center;
    align-items: space-around;
  }
  #header-major .header-major__content__figure {
    height: 100px;
    width: 100px;
    border-radius: 50%;
    padding: 0;
    order: 1;
  }
  #header-major .header-major__content__figure__img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }
  #header-major .header-major__content__figure__figcaption {
    display: none;
  }
  #header-major .header-major__content__wrapper-text {
    width: 100%;
    align-items: center;
    order: 2;
    padding: 0 5px 0 5px;
  }
  #header-major .header-major__content__wrapper-text h2 {
    margin: 15px 0 15px 0;
  }
  #header-major .header-major__content__wrapper-text__mobile-version {
    display: block;
  }
  #header-major .header-major__content__wrapper-text p {
    text-align: center;
    margin-top: 5px;
  }
  #header-major .header-major__content__wrapper-text__contacts {
    align-items: center;
    position: relative;
  }
  #header-major .header-major__content__wrapper-text__contacts__icon {
    width: 15px;
    height: 18px;
  }
  #header-major .header-major__content__wrapper-text__contacts p {
    font-size: 16px;
    margin: 0;
  }
}
@media (max-width: 575px) {
  #header-major {
    background-image: url('../../build/img/header_background-image600x399.png');
  }
  #header-major .header-major__content {
    flex-direction: column;
    justify-content: center;
    align-items: space-around;
  }
  #header-major .header-major__content__wrapper-text {
    width: 100%;
    align-items: center;
    order: 2;
    padding: 0 5px 0 5px;
  }
  #header-major .header-major__content__wrapper-text h2 {
    margin: 10px 0 10px 0;
  }
  #header-major .header-major__content__wrapper-text p {
    text-align: center;
    margin-top: 5px;
  }
  #header-major .header-major__content__wrapper-text__contacts {
    align-items: center;
    position: relative;
  }
  #header-major .header-major__content__wrapper-text__contacts__icon {
    width: 15px;
    height: 18px;
  }
  #header-major .header-major__content__wrapper-text__contacts p {
    margin: 0;
  }
}
/* end #header-major */
@media (max-width: 1199px) {
  #main-skills .main-skills__content__cards__item {
    width: 200px;
    height: 180px;
  }
  #main-skills .main-skills__content__cards__item__content img {
    width: 50px;
    height: 50px;
  }
}
@media (max-width: 991px) {
  #main-skills .main-skills__content__cards__item {
    width: 180px;
    height: 160px;
  }
  #main-skills .main-skills__content__cards__item__content img {
    width: 40px;
    height: 40px;
  }
}
@media (max-width: 767px) {
  #main-skills .main-skills__content__cards__item {
    width: 140px;
    height: 110px;
  }
  #main-skills .main-skills__content__cards__item__content img {
    width: 30px;
    height: 30px;
  }
}
@media (max-width: 575px) {
  #main-skills {
    height: auto;
  }
  #main-skills .main-skills__content {
    border: none;
  }
  #main-skills .main-skills__content::after {
    display: none;
  }
  #main-skills .main-skills__content::before {
    display: none;
  }
  #main-skills .main-skills__content h1 {
    text-align: center;
    margin: 10px 0 10px 0;
  }
  #main-skills .main-skills__content__cards__item {
    width: 95%;
    height: 90px;
    margin: 5px 5px;
  }
  #main-skills .main-skills__content__cards__item__hover {
    transform: none;
    background-color: none;
    border: none;
  }
  #main-skills .main-skills__content__cards__item__content {
    flex-direction: row;
    justify-content: flex-start;
  }
  #main-skills .main-skills__content__cards__item__content ul {
    opacity: 1;
    top: 0;
    left: auto;
    right: 0;
    width: 50%;
    margin: 0;
    color: white;
  }
  #main-skills .main-skills__content__cards__item__content ul li {
    font-weight: normal;
    font-size: 10px;
    color: white;
  }
  #main-skills .main-skills__content__cards__item__content img {
    width: 35px;
    height: 35px;
    margin: 0 5px;
  }
  #main-skills .main-skills__content__cards__item__content h2 {
    margin: 0;
  }
}
